  margin: 0;
  outline: none;
  padding: 0;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, nav, section {
  display: block;
}

del {
  text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
  border: medium none;
