  width: 33.33%;
}

.eltdf-vertical-align-containers .eltdf-position-left {
  position: relative;
  height: 100%;
  float: left;
  z-index: 2;
}

.eltdf-vertical-align-containers .eltdf-position-center {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: 1;
}

.eltdf-vertical-align-containers .eltdf-position-right {
