@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
    
    .eltdf-comments-title {
        margin-bottom: 28px;
    }

    .eltdf-comment {

        &.eltdf-no-avatar {

            .eltdf-comment-text {
                padding: 0;
            }
        }
    }

    .eltdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 149px;
        }
    }

    .eltdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 112px;
        height: 112px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .eltdf-comment-text {
        padding: 2px 6px 0 149px;
        min-height: 112px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 18px;
            font-size: 22px;
            color: $first-main-color;

            &:hover {
                color: $darker-main-color;
            }
        }

        .comment-reply-link {

            &:before {
                content: '\f21e';
                font-family: 'Ionicons';
            }
        }

        .comment-edit-link {
            font-size: 17px;

            &:before {
                content: '\f2bf';
                font-family: 'Ionicons';
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 18px;
        }

        .eltdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }

            .comment-form {

                .eltdf-btn {
                    padding: 10px 71px;
                }
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $darker-main-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    @include eltdfRelativeHolderLayout();
    margin: 106px 0 40px;
    clear: both;

    .comment-reply-title {
        margin: 0 0 17px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    .comment-form {

        .comment-form-cookies-consent {
            margin: 0;

            #wp-comment-cookies-consent {
                margin: 4px 8px 4px 0px;
            }
        }

        .eltdf-btn {
            margin-top: 22px;
            padding: 10px 71px;
        }
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {

            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
	
	&.wpcf7-submit {
		margin-top: 21px;
	}
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonSize();
    @include eltdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

#respond input[type="text"]:last-of-type:not(:last-child) {
    margin-bottom: 0;
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.eltdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.eltdf-slider-is-moving {
        .owl-item {
            .eltdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.eltdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            transform: translateY(-50%);
            transition: color .2s ease-out;

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;

            > span {
                @include eltdfPopoutSVGArrows();
            }

            &:hover {

                > span {
                    @include eltdfPopoutSVGArrowsHover();
                }
            }
        }

        .owl-next {
            right: 30px;

            > span {
                @include eltdfPopoutSVGArrows();
            }

            &:hover {

                > span {
                    @include eltdfPopoutSVGArrowsHover();
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 12px 0 0;
        counter-reset: dots;

        .owl-dot {
            position: relative;
            bottom: 0;
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 19px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            overflow: hidden;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 1.9em;
                background-color: transparent;
                border: none;
                padding-top: 5px;
                box-sizing: content-box;

                &:before {
                    box-sizing: content-box;
                    content: counter(dots);
                    counter-increment: dots;
                    position: absolute;
                    top: calc(100% - 1.5em);
                    left: 2px;
                    font-family: $default-heading-font;
                    font-size: 12px;
                    font-weight: 700;
                    letter-spacing: .23em;
                    color: $first-main-color;
                    transition: top .2s ease-out .2s, color .2s ease-in-out;
                }
            }

            &:after {
                box-sizing: content-box;
                content: '';
                display: block;
                width: 100%;
                height: 3px;
                border-top: 1px solid $first-main-color;
                border-bottom: 1px solid $first-main-color;
                position: relative;
                left: -1px;
                bottom: 0;
                transform: translateY(100%);
                transition: transform .2s ease-out .1s;
            }

            &:hover {

                span {

                    &:before {
                        color: #e3c59b;
                    }
                }
            }

            &.active {

                span {

                    &:before {
	                    top: .25em;
                        transition: top .2s ease-in;
                    }
                }

                &:after {
                    transform: translateY(-6px);
                    transition: transform .2s ease-in .1s;
                }

                &:hover {

                    span {

                        &:before {
                            color: $first-main-color;
                        }
                    }
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .eltdf-wrapper {
        padding: 0 !important;
    }

    .eltdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .eltdf-content {
            height: calc(100vh - 32px);
        }
    }

    .eltdf-content-inner {
        @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
        @include eltdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .eltdf-404-title {
            margin: 0;
        }

        .eltdf-404-subtitle {
            margin: 35px 0 0;
        }

        .eltdf-404-text {
            margin: 10px 0 0;
        }

        .eltdf-btn {
            margin: 30px 0 0;

            &.eltdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .eltdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .eltdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    right: 33px;
    bottom: 33px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    svg {

        circle {
            stroke-width: 1.4;

            &.eltdf-popout {
                stroke-dashoffset: 101;
                stroke-dasharray: 101;
                stroke-linejoin: round;
                stroke-miterlimit: 10;
                stroke-linecap: butt;
                transform: rotate(-90deg);
                transform-origin: center;
                transition: all .7s ease;
            }
        }

        polyline {
            stroke-width: 1.4;
        }
    }

    &:hover {

        .eltdf-popout {
            stroke-dashoffset: 0;
            stroke-dasharray: 101;
            transition: all .7s ease;
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

