/* ==========================================================================
   #SVG Row Pattern - start
   ========================================================================== */

.eltdf-elementor-row-with-pattern{
    display: block;
    position: relative;
    width: 100%;
}

.eltdf-svg-pattern-holder {
    position: absolute;

    //set z index since in elementor all sections are relative
    .elementor-inner & {
        z-index: 1;
    }

    &.eltdf-pattern-position-left {
        left: 0;
    }

    &.eltdf-pattern-position-right {
        right: 0;
    }

    svg {

        path {
            stroke-dashoffset: 372;
            stroke-dasharray: 372;
            transition: all 2s ease;
        }
    }

    &.eltdf-appear {

        svg {

            path {
                stroke-dashoffset: 0;
                stroke-dasharray: 372;
                transition: all 2s ease;
            }
        }
    }

    @include ipad-portrait {
        display: none;
    }
}

/* ==========================================================================
   #SVG Row Pattern - end
   ========================================================================== */