/* #Reset default styles - start
   ========================================================================== */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font,
ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    background: transparent;
    border: 0;
    margin: 0;
    outline: none;
    padding: 0;
    vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, nav, section {
    display: block;
}

del {
    text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border: medium none;
    border-collapse: collapse;
    border-spacing: 0;
    vertical-align: middle;
}

table th {
    border: 0;
    padding: 5px 7px;
    text-align: center;
}

table td {
    padding: 5px 10px;
    text-align: center;
}

a img {
    border: none;
}

img, .wp-caption {
    height: auto;
    max-width: 100%;
}

iframe {
    max-width: 100%;
}

/* #Reset default styles - end
   ========================================================================== */

/* #General styles that are used in our theme - start
 * Here are defined global styles on body element, on container elements that hold every page,
 * headings and links, inputs, core wp styles and default pagination styles
   ========================================================================== */

@font-face {
    font-family: 'caviar_dreamsbold';
    src: url('../fonts/caviardreams_bold-webfont/caviardreams_bold-webfont.eot');
    src: url('../fonts/caviardreams_bold-webfont/caviardreams_bold-webfont.eot?#iefix') format('embedded-opentype'),
    url('../fonts/caviardreams_bold-webfont/caviardreams_bold-webfont.woff2') format('woff2'),
    url('../fonts/caviardreams_bold-webfont/caviardreams_bold-webfont.woff') format('woff'),
    url('../fonts/caviardreams_bold-webfont/caviardreams_bold-webfont.ttf') format('truetype'),
    url('../fonts/caviardreams_bold-webfont/caviardreams_bold-webfont.svg#caviar_dreamsbold') format('svg');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'metropolis1920regular';
    src: url('../fonts/metropolis1920-regular-webfont/metropolis1920-regular-webfont.eot');
    src: url('../fonts/metropolis1920-regular-webfont/metropolis1920-regular-webfont.eot?#iefix') format('embedded-opentype'),
    url('../fonts/metropolis1920-regular-webfont/metropolis1920-regular-webfont.woff2') format('woff2'),
    url('../fonts/metropolis1920-regular-webfont/metropolis1920-regular-webfont.woff') format('woff'),
    url('../fonts/metropolis1920-regular-webfont/metropolis1920-regular-webfont.ttf') format('truetype'),
    url('../fonts/metropolis1920-regular-webfont/metropolis1920-regular-webfont.svg#metropolis1920regular') format('svg');
    font-weight: normal;
    font-style: normal;
}

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

.clearfix {
    *zoom: 1;
}

.clear:after {
    clear: both;
    content: "";
    display: block;
}

html {
    overflow-y: scroll !important; /* for vertical split screen slider shortcode on touch devices */
}

body {
    @include eltdfBody();
    overflow-x: hidden !important;
}

h1 {
    @include eltdfH1();
}

h2 {
    @include eltdfH2();
}

h3 {
