}

.text-align-right {
    text-align: right;
}

.text-align-left {
    text-align: left;
}

.text-align-center {
    text-align: center;
}

.eltdf-wrapper {
    position: relative;
    left: 0;
    z-index: 1000;
    transition: left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1);

    .eltdf-wrapper-inner {
        width: 100%;
        overflow: hidden;
    }
}

.eltdf-content {
    position: relative;
    margin-top: 0;
    background-color: $default-background-color; /*this is needed because of the uncovering footer*/
    z-index: 100;
}

.eltdf-boxed {
    overflow: hidden !important; /* this is because content jumping while using accordion and display:inline-block on 'body.eltdf-boxed .eltdf-wrapper'   */

    .eltdf-wrapper {
        width: 100%;
        display: inline-block;
        vertical-align: middle;

        .touchevents & {
            background-repeat: repeat;
        }

        .eltdf-wrapper-inner {
            width: $grid-width-boxed;
            margin: auto;
            overflow: visible;
        }
    }

    .eltdf-content {
        overflow: hidden;
    }

    .eltdf-footer-inner {
        width: $grid-width-boxed;
        margin: auto;
    }

    .eltdf-page-header .eltdf-sticky-header,
    .eltdf-page-header .eltdf-fixed-wrapper.fixed {
        left: auto;
        width: $grid-width-boxed;
    }
}

.eltdf-paspartu-enabled {
    
    .eltdf-wrapper {
        padding: 10px;
        background-color: #fff;
        box-sizing: border-box;
    }

    &.eltdf-top-paspartu-disabled {
        
        .eltdf-wrapper {
            padding-top: 0 !important;
        }
    }

    .eltdf-page-header {
        
        .eltdf-fixed-wrapper {
            
            &.fixed {
                left: 10px;
                width: calc(100% - 20px);
            }
        }
    }

    .eltdf-sticky-header {
        left: 10px;
        width: calc(100% - 20px);
    }

    .eltdf-mobile-header {
        
        &.mobile-header-appear {
            
            .eltdf-mobile-header-inner {
                left: 10px;
                width: calc(100% - 20px);
            }
        }
    }

    &.eltdf-fixed-paspartu-enabled {
        
        &.admin-bar {
            
            .eltdf-wrapper {
                
                &:before {
                    top: 32px;
                }
            }
        }

        .eltdf-wrapper {
            
            &:before,
            &:after {
                content: '';
                position: fixed;
                left: 0;
                width: 100%;
                height: auto;
                padding-top: inherit;
                background-color: inherit;
                z-index: 99999;
            }

            &:before {
                top: 0;
            }

            &:after {
                bottom: 0;
            }
        }

        .eltdf-page-header {
            
            .eltdf-fixed-wrapper {
                
                &.fixed {
                    margin-top: 10px;
                }
            }
        }

        .eltdf-sticky-header {
            
            &.header-appear {
                margin-top: 10px;
            }
        }

        .eltdf-mobile-header {
            
            &.mobile-header-appear {
                
                .eltdf-mobile-header-inner {
                    margin-top: 10px;
                }
            }
        }
    }
}

.eltdf-smooth-transition-loader {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: #090d0e;
    z-index: 2000;
    opacity: 1;
    display: block;
    transition: opacity .5s cubic-bezier(0.48, 0.57, 0.33, 0.89);

    &.eltdf-finished-animation {
        opacity: 0;
        pointer-events: none;
    }
}

.eltdf-container {
    position: relative;
    width: 100%;
    padding: 0;
    z-index: 100;

    &:before {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
        content: " ";
        display: table;
    }
}

.eltdf-container-inner { //not in parent selector since this class is used for positioning other elements in grid
    width: $grid-width;
    margin: 0 auto;
    box-sizing: border-box;
}

.eltdf-full-width {
    position: relative;
    z-index: 100;
}

.eltdf-content .eltdf-content-inner > .eltdf-container > .eltdf-container-inner,
.eltdf-content .eltdf-content-inner > .eltdf-full-width > .eltdf-full-width-inner {
