/* #General styles that are used in our theme - end
   ========================================================================== */

@include keyframes(preload-background) {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 100% 0;
    }
}

.eltdf-preload-background {
    background: transparent url('../img/preload_pattern.png') repeat scroll inherit !important;
    animation-name: preload-background;
    animation-duration: 40s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-fill-mode: forwards; // this prevents the animation from restarting!
}

.eltdf-grid {
    margin: 0 auto;
    width: $grid-width;
}

.eltdf-vertical-align-containers {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 20px;
    box-sizing: border-box;

    &.eltdf-25-50-25 {
        
        .eltdf-position-left,
        .eltdf-position-right {
            width: 25%;
        }

        .eltdf-position-center {
            left: 50%;
            width: 50%;
            transform: translateX(-50%);
        }
    }

    &.eltdf-33-33-33 {
        
        .eltdf-position-left,
        .eltdf-position-center,
        .eltdf-position-right {
            width: 33.33%;
        }

        .eltdf-position-center {
            left: 50%;
            width: 50%;
            transform: translateX(-50%);
        }
    }

    &.eltdf-50-50 {
        
        .eltdf-position-left,
        .eltdf-position-right {
            width: 50%;
        }
    }

    &.eltdf-33-66 {
        
        .eltdf-position-left {
            width: 33.33%;
        }

        .eltdf-position-right {
            width: 66.66%;
        }
    }

    &.eltdf-66-33 {
        
        .eltdf-position-left {
            width: 66.66%;
        }

        .eltdf-position-right {
            width: 33.33%;
        }
    }

    .eltdf-position-left {
        position: relative;
        height: 100%;
        float: left;
        z-index: 2;
    }

    .eltdf-position-center {
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center;
        z-index: 1;
    }

    .eltdf-position-right {
        position: relative;
        height: 100%;
        float: right;
        text-align: right;
        z-index: 2;
    }

    .eltdf-position-left-inner,
    .eltdf-position-center-inner,
    .eltdf-position-right-inner {
        display: inline-block;
        vertical-align: middle;
    }

    .eltdf-position-right.eltdf-has-widget .eltdf-position-right-inner {
        height: 100%;
    }

    .eltdf-position-left:before,
    .eltdf-position-center:before,
    .eltdf-position-right:before {
        content: "";
        height: 100%;
        display: inline-block;
        vertical-align: middle;
        margin-right: 0; /* adjusts for spacing before it was -3px */
    }
}

