.eltdf-rev-has-paspartu {
    @include eltdfRelativeHolderLayout();
    height: 100vh;
    margin: 0;
    box-sizing: border-box;

    .admin-bar & {
        height: calc(100vh - 32px);
    }

    &.eltdf-side-paspartu-disabled {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    &.eltdf-paspartu-tiny {
        padding: 10px;

        &.eltdf-top-paspartu-disabled {
            padding-top: 0;

            .forcefullwidth_wrapper_tp_banner {
                
                .tp-fullwidth-forcer {
                    height: calc(100vh - 10px) !important;

                    .admin-bar & {
                        height: calc(100vh - 42px) !important;
                    }
                }
            }
        }

        .forcefullwidth_wrapper_tp_banner {
            
            .tp-fullwidth-forcer {
                height: calc(100vh - 20px) !important;

                .admin-bar & {
                    height: calc(100vh - 52px) !important;
                }
            }
        }
    }

    &.eltdf-paspartu-small {
        padding: 20px;

        &.eltdf-top-paspartu-disabled {
            padding-top: 0;

            .forcefullwidth_wrapper_tp_banner {
                
                .tp-fullwidth-forcer {
                    height: calc(100vh - 20px) !important;

                    .admin-bar & {
                        height: calc(100vh - 52px) !important;
                    }
                }
            }
        }

        .forcefullwidth_wrapper_tp_banner {
            
            .tp-fullwidth-forcer {
                height: calc(100vh - 40px) !important;

                .admin-bar & {
                    height: calc(100vh - 72px) !important;
                }
            }
        }
    }

    &.eltdf-paspartu-normal {
        padding: 30px;

        &.eltdf-top-paspartu-disabled {
            padding-top: 0;

            .forcefullwidth_wrapper_tp_banner {
                
                .tp-fullwidth-forcer {
                    height: calc(100vh - 30px) !important;

                    .admin-bar & {
                        height: calc(100vh - 62px) !important;
                    }
                }
            }
        }

        .forcefullwidth_wrapper_tp_banner {
            
            .tp-fullwidth-forcer {
                height: calc(100vh - 60px) !important;

                .admin-bar & {
                    height: calc(100vh - 92px) !important;
                }
            }
        }

        @include phone-landscape {
            padding: 20px;

            &.eltdf-top-paspartu-disabled {
                padding-top: 0;

                .forcefullwidth_wrapper_tp_banner {
                    
                    .tp-fullwidth-forcer {
                        height: calc(100vh - 20px) !important;

                        .admin-bar & {
                            height: calc(100vh - 52px) !important;
                        }
                    }
                }
            }

            .forcefullwidth_wrapper_tp_banner {
                
                .tp-fullwidth-forcer {
                    height: calc(100vh - 40px) !important;

                    .admin-bar & {
                        height: calc(100vh - 72px) !important;
                    }
                }
            }

        }
    }

    &.eltdf-paspartu-large {
        padding: 50px;

        &.eltdf-top-paspartu-disabled {
            padding-top: 0;

            .forcefullwidth_wrapper_tp_banner {
                
                .tp-fullwidth-forcer {
                    height: calc(100vh - 50px) !important;

                    .admin-bar & {
                        height: calc(100vh - 82px) !important;
                    }
                }
            }
        }

        .forcefullwidth_wrapper_tp_banner {
            
            .tp-fullwidth-forcer {
                height: calc(100vh - 100px) !important;

                .admin-bar & {
                    height: calc(100vh - 132px) !important;
                }
            }
        }

        @include phone-landscape {
            padding: 20px;

            &.eltdf-top-paspartu-disabled {
                padding-top: 0;

                .forcefullwidth_wrapper_tp_banner {
                    
                    .tp-fullwidth-forcer {
                        height: calc(100vh - 20px) !important;

                        .admin-bar & {
                            height: calc(100vh - 52px) !important;
                        }
                    }
                }
            }

            .forcefullwidth_wrapper_tp_banner {
                
                .tp-fullwidth-forcer {
                    height: calc(100vh - 40px) !important;

                    .admin-bar & {
                        height: calc(100vh - 72px) !important;
                    }
                }
            }

        }
    }

    .forcefullwidth_wrapper_tp_banner {
        left: 0 !important;
        width: 100% !important;

        .rev_slider_wrapper {
            left: 0 !important;
            width: 100% !important;
            height: 100% !important;
        }

        .tp-fullwidth-forcer {
            height: 100vh !important;

            .admin-bar & {
                height: calc(100vh - 32px) !important;
            }
        }
    }
}

/* ==========================================================================
   #VC Revolution Slider styles - end
   ========================================================================== */

/* ==========================================================================
   #Screen reader text - begin
   ========================================================================== */

/* Text meant only for screen readers. */
.screen-reader-text {
    position: absolute !important;
    width: 1px;
    height: 0;
    clip: rect(1px, 1px, 1px, 1px);
    word-wrap: normal !important; /* Many screen reader and browser combinations announce broken words as they would appear visually. */
    overflow: hidden;

    &:focus {
        top: 5px;
        left: 5px;
        display: block;
        width: auto;
        height: auto;
        padding: 15px 23px 14px;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        text-decoration: none;
        color: #21759b;
        background-color: #f1f1f1;
        border-radius: 3px;
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
        clip: auto !important;
        z-index: 100000; /* Above WP toolbar. */
    }
}

/* ==========================================================================
   #Screen reader text - end
   ========================================================================== */

/* ==========================================================================
   #Grid Lines - begin
   ========================================================================== */

.eltdf-grid-lines-holder {
    position: absolute;
    top: 0;
    left: 110px;
    width: calc(100% - 220px);
    height: 100%;
    z-index: -1;

    .eltdf-grid-line {
        display: block;
        float: left;
        height: 100%;
        box-sizing: border-box;
    }

    .eltdf-grid-line:not(:first-child) {
        border-left: 1px solid #21221e;
    }

    &.eltdf-grid-columns-4 {

        .eltdf-grid-line {
            width: 25%;
        }
    }

    &.eltdf-grid-columns-5 {

        .eltdf-grid-line {
            width: 20%;
        }
    }

    &.eltdf-grid-columns-6 {

        .eltdf-grid-line {
            width: 16.666%;
        }
    }
}

.eltdf-double-grid-line-one,
.eltdf-double-grid-line-two {
    position: fixed;
    display: block;
    top: 110px;
    width: 0;
    height: 100vh;
    border-left: 1px solid $default-border-color;
    z-index: 109;
}

.eltdf-double-grid-line-one,
.eltdf-header-double-grid-line-one {
    left: 110px;
}

.eltdf-double-grid-line-two,
.eltdf-header-double-grid-line-two {
    right: 110px;
}

.eltdf-header-double-grid-line-one,
.eltdf-header-double-grid-line-two {
    position: fixed;
    display: block;
    top: 0;
    width: 0;
    height: 110px;
    border-left: 1px solid $default-border-color;
    z-index: 200;

    .admin-bar & {
        top: 32px;
    }
}

/* ==========================================================================
   #Grid Lines - end
   ========================================================================== */